import { z } from 'zod'

// Registreringsskjema validering
export const registerSchema = z.object({
  fullName: z.string().min(2, 'Fullt navn m vre minst 2 tegn'),
  birthDate: z.string().refine(date => {
    const parsedDate = new Date(date)
    return !isNaN(parsedDate.getTime())
  }, 'Ugyldig dato'),
  company: z.string().min(2, 'Firmanavn m vre minst 2 tegn'),
  jobTitle: z.string().min(2, 'Stillingstittel m vre minst 2 tegn'),
  email: z.string().email('Ugyldig e-postadresse'),
  city: z.string().min(2, 'By m vre minst 2 tegn'),
  gender: z.enum(['male', 'female'], {
    errorMap: () => ({ message: 'Vennligst velg kjnn' })
  }),
  password: z.string().min(8, 'Passord m vre minst 8 tegn'),
  confirmPassword: z.string().min(8, 'Bekreft passord m vre minst 8 tegn')
}).refine(data => data.password === data.confirmPassword, {
  message: 'Passordene m vre like',
  path: ['confirmPassword']
})

export type RegisterFormValues = z.infer<typeof registerSchema>

// Innloggingsskjema validering
export const loginSchema = z.object({
  email: z.string().email('Ugyldig e-postadresse'),
  password: z.string().min(1, 'Passord er pkrevd')
})

export type LoginFormValues = z.infer<typeof loginSchema> 